<template>
  <div style="padding:20px;">
    <Button type="primary" icon="ios-plus-empty" @click="handleAddArticleType">
          添加内容
        </Button>
        <div style="height:20px;"></div>
    <Table :loading="loading" border ref="selection" :columns="articlecolumns" :data="articledata">

    </Table>
    <div style="margin-top: 15px;overflow: hidden">
      <div style="float: left;">
      </div>
      <div style="float: right;">
        <!-- <Page :total="totalrecord" :page-size="pagesize"  show-elevator show-sizer :page-size-opts="[10,20,30,40]" placement="top" show-total @on-change='handleIndexChange' @on-page-size-change='handleSizeChange'></Page> -->
      <Page :total="totalcount" :page-size="page.pagesize"  :page-size-opts="[10,20,30,40]" show-elevator show-sizer placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange'></Page>
      </div>
    </div>
    <Modal title="确认提示" v-model="showConfirm" width="300" class-name="vertical-center-modal" @on-ok="delRow">
      <p style="font-size:14px">确认要删除该条记录吗？</p>
    </Modal>
  </div>
</template>
<script>
export default {
  data() {
    return {
      showConfirm: false,
      currrowid: 0,
      totalcount: 0,
      loading:false,
      page:{
        pagesize:10,
        pageindex:0
      },
      articletype:[],
      articlecolumns: [{
          title: '#',
          width: 60,
          align: 'center',
          render:(h,params)=>params.index+this.page.pagesize*this.page.pageindex+1
        },
        {
          title: "标题图片",
          width: 100,
          align: "center",
          render: (h, params) => {
            if (params.row.picture) {
              return h("img", {
                attrs: {
                  src: params.row.picture,
                  style:
                    "width: 70px;height: 70px;border-radius: 5px;vertical-align: middle;margin:5px 0;"
                }
              });
            } else {
              return h("div", {
                attrs: {
                  style:
                    "width: 70px;height: 70px;border:1px solid #666;margin:5px 0;vertical-align: middle;display:inline-block"
                }
              });
            }
          }
        },
        {
          title: '标题名称',
          key: 'title',
          align:'center',
        },
        // {
        //   title:'分类',
        //   align:'center',
        //   width:150,
        //   render:(h,params) => this.showType(params.row.typeid)
        // },
        {
          title: '修改日期',
          key: 'updatetime',
          align:'center',
          width: 220
        },
        {
          title: '操作',
          key: 'action',
          width: 150,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',

                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    //this.show(params.index)
                    this.showPage(params.row.id);

                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                },
                on: {
                  click: () => {
                    //this.remove(params.index)
                    this.showConfirm = true;
                    this.currrowid = params.row.id;
                  }
                }
              }, '删除')
            ]);
          }
        }
      ],
      articledata: []
    }
  },
  mounted: function() {
    this.$sitemap.prvname = "内容管理";
    this.$sitemap.currname = '内容列表';
    this.$menuactive.open = ['ArticleBlock'];
    this.$menuactive.active = 'ArticleList';
    this.$Loading.start();
    // Promise.all(this.getArticleList(),this.getArticleTypeList())
    this.getArticleList();
    // this.getArticleTypeList();
  },
  watch:{
    page:{
      handler(newval) {
        this.getArticleList()
      },
      deep:true,
    }
  },
  methods: {
    indexChange(index) {
      this.page.pageindex = index-1
    },
    sizeChange(size) {
      this.page.pagesize = size
    },
    showType(id) {
      let filter=this.articletype.find(it=>it.id===id)
      return filter?filter.name:''
    },
    handleAddArticleType() {
      this.$router.push('/article/add')
    },
    handleSelectAll(status) {
      this.$refs.selection.selectAll(status);
    },
    getArticleList() {
      let that = this;
      this.loading = true
     this.$http.post("article/list", this.page).then(function(res) {
        that.articledata = res.data.data;
        that.loading = false;
        that.$Loading.finish();
        that.totalcount = +res.data.totalcount
      }).catch(function(res) {
        that.loading = false;
        that.$Loading.error();
      })
    },
    showPage(id){
       this.$router.push({ path: '/article/edit', query: { id: id} });
    },
    getArticleTypeList() {
      var that = this;
      this.$http.post("articletype/list", {}).then(function(res) {
        that.articletype = res.data.data;
        that.$Loading.finish();
      });
    },
    delRow() {
      var that = this;
      var params = { artid: this.currrowid,pagesize:10,
        pageindex:0  };
      this.$http.post("article/del", params).then(res=> {
        if (res.data.result == 1) {
          that.$Message.success('删除成功!');
          that.getArticleList();
        } else {
          that.$Message.error('删除失败，请检查！');
        }
      }).catch(error =>{
        that.loading = false;

        that.$Message.error(error);
      })
    }
  }
}

</script>
<style lang="less">

.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;

  .ivu-modal {
    top: 0;
  }
}

</style>



